#projects{
  display: flex;
  gap: 8px;

  img{
    width: 50%;
    object-fit: cover;
  }

  @media (max-width: 768px) {
    flex-direction: column;

    img{
      height: max(30vh, 300px, 50vw);
      width: 100%;
    }
  }
}

.project{
  box-sizing: border-box;
  background: var(--primary-color);
  width: 100%;
  display: flex;
  padding: 32px 64px;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  gap: 160px;

  @media (max-width: 500px) {
    padding: 32px;
  }

  @media (max-width: 768px) {
    gap: 64px;
  }
}

.projectHeader{
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  gap: 32px;

  button{
    width: 100%;
    gap: 16px;
  }

  @media (max-width: 500px) {
    flex-direction: column;
    gap: 16px;
  }
}

.projectBody{
  display: flex;
  flex-direction: column;
  gap: 24px;

  a{
    color: white;
  }

  div{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: fit-content;
  }

  div:nth-child(2){
    gap: 16px;
  }

  @media (max-width: 768px) {
    align-items: center;
    text-align: center;

    div{
      align-items: center;
    }

    button:nth-child(1){
      width: 100%;
    }
  }
}

.projectHeader, .projectBody{
  width: 100%;
  color: var(--white-color);

  button{
    display: flex;
    padding: 16px 32px;
    box-sizing: border-box;

    h5{
      font-weight: 600;
    }

    *{
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}

.projectFooter{
  display: flex;
  width: 100%;

  button{
    width: 100%;
    display: flex;
    padding: 16px 64px;
    align-items: flex-start;
    gap: 32px;
  }

  p{
    font-weight: 600;
  }

  @media (max-width: 500px) {
    button{
      gap: 16px;
      padding: 16px;
      flex-direction: column;
    }
  }
}